import React from 'react';
import './App.css';
import Editor from './Editor';

function App() {
    const params = [
        {
            id: 1,
            name: "",
            type: "string",
        },
        {
            id: 2,
            name: "",
            type: "string",
        },
    ];
    const model = {
        paramValues: [
            {
                paramId: 1,
                value: "",
            },
            {
                paramId: 2,
                value: "",
            },
        ],
        colors: [
            {
                id: 1,
                name: "green",
            },
            {
                id: 2,
                name: "orange",
            },
        ],
    };
  return (
    <div className="App">
      <header className="App-header">
        <h1></h1>
      </header>
      <Editor params={params} model={model}/>
    </div>
  );
}

export default App;
